import React, { Component } from 'react';
import pd_png from '../../../assets/image/pd.png';
import { Carousel, List, Button } from 'antd-mobile';
import CustomWhiteSpace from '../../../component/customWhiteSpace';
import TitleContent from '../../../component/titleContent';
import fullScreen from '../../../component/fullScreen';
import styles from './index.less';

export default fullScreen(class counselor extends Component {

    componentDidMount() {
        const { location } = this.props,
            { state = {} } = location,
            { name = '顾问详情' } = state;
        document.title = name;
    }

    render() {
        return (
            <div className={styles.wrapper}>
                <div className={styles.wrapper_container}>
                    <Carousel
                        autoplay={false}
                        infinite
                        className='carousel_common'
                    >
                        {[pd_png, pd_png, pd_png].map((val, index) => (
                            <span
                                key={index}
                                className='carousel_common_item'
                            >
                                <img
                                    src={val}
                                    alt=""
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                    }}
                                />
                            </span>
                        ))}
                    </Carousel>
                    <List renderHeader={() => <ul className={styles.wrapper_container_info}>
                        <li className='titleFontSizeC'>
                            <div>
                                <img src={pd_png} alt='' />
                            </div>
                            李金丽
                        </li>
                        <li className='normalFontSizeC'>
                            <i className='iconfont icon-phone' />
                            13597456698
                            <i className='iconfont icon-code' />
                        </li>
                    </ul>}>
                        <List.Item thumb={<i className='iconfont icon-store redColor' />} arrow='horizontal'>东莞南城金隅小区</List.Item>
                    </List>
                    <CustomWhiteSpace />
                    <TitleContent title='精选方案'>

                    </TitleContent>
                </div>
                <div className={styles.wrapper_footer}>
                    <Button type='warning'>预约量房</Button>
                </div>
            </div>
        );
    }
})